@import '../../scss/variables';

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
   

    .cards{
        background-color: $cardNavBgColor;
        margin: 1rem;
        padding: 1rem;
        border-radius: 10px;
        box-shadow: 3px 3px 5px rgba(6, 6, 6, 0.7);
        position: relative;
        overflow: hidden;
        &:hover .card-over{
            transform: translateY(0%);
        }
    }

    .title{
        color: #72f8f6;
        font-size: 1rem;
        padding: 1rem;
    }

    img{
        width: 400px;
        height: 250px;
        object-fit: cover;
        filter: grayscale(0);
        &:hover{
            filter: grayscale(100);
        }
    }

    .card-over{
        color: white;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 1.5rem;
        background-color: rgb(4, 58, 62);
        padding: 1rem;
        transform: translateY(100%);
        z-index: 1;
        transition: transform 0.7s ease-in-out;
    }
    }

    














